<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="t_bar">
          <yd-flexbox class="t_bar_num">
              <yd-flexbox-item>
                <span class="t_bar_num_top">{{$t('tuiguang.friend')}}</span>
                <span class="t_bar_num_bottom">{{list.totalCount}}</span>
              </yd-flexbox-item>
              <yd-flexbox-item>
                <span class="t_bar_num_top">{{$t('tuiguang.jiangli')}}</span>
                <span class="t_bar_num_bottom">{{list.totalSum}}</span>
              </yd-flexbox-item>
          </yd-flexbox>
        </div>
        <yd-cell-group>
            <yd-cell-item arrow type="link" href="/tuiguang">
                <span slot="left">A级</span>
                <span slot="right">{{list.ANum}}</span>
            </yd-cell-item>
        </yd-cell-group>
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
  import footerBar from './footer.vue'
  import {getCookie,setCookie} from '../assets/js/cookie.js'
export default {
  components:{
      "headerBar":headerBar,
      "footerBar":footerBar,
  },
  data () {
    return {
      headerNavData:{
         left:true,
         title:this.$t('mycenter.team'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      },
      list:''
    }
  },
  created(){
    this.team()
  },
  methods:{
     team(){
        this.openloading('加载中...')
        var that = this;
        this.axios({
          url:this.Url + 'Extension.ashx',
          method:'post',
          params: {req:7,publickey:getCookie("publickey")} 
        }).then(function (res) {
          that.closeloading()
            var _list = res.data.data;
            if (_list) {
              that.list = _list
            }
        })

     },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.yingyong{
  padding-top: .9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
}

.yd-cell-item{
  border-bottom:1px solid #f4f4f4;
}
.yd-cell-item span:nth-child(2){
  font-size: .3rem;
  color: #333;
}
.t_bar{
  margin: .42rem .2rem;
  box-shadow: 0 5px 10px #a9c2d8;
  background: #fff;
  text-align: center;
  border-radius: 6px;
  padding: .2rem 0;
}
.t_bar_num .yd-flexbox-item:nth-child(1){
  border-right: 1px solid #f4f4f4;
}
.t_bar_num_top{
  font-size: .3rem;
  color: #333;
  display: block;
  margin-top: .1rem;
  margin-bottom: .2rem;
}
.t_bar_num_bottom{
  font-size: .28rem;
  color: #278eed;
  display: block;
  margin-top: .2rem;
  margin-bottom: .1rem;
}
</style>



